import { animate, transition, trigger, group, state, style } from '@angular/animations';

export function getFadeTrigger() {
    return trigger('fade', [
        transition(':leave', [
            style({ position: 'absolute', top: '2px', opacity: 1, transform: 'translateX(0%)' }),
            animate('500ms ease-in', style({ opacity: 0, transform: 'translateX(1000%)' })),
            style({ display: 'none' })
        ]),
        transition(':enter', [
            style({ opacity: 0, transform: 'translateX(-1000%)' }),
            animate('500ms ease-out', style({ opacity: 1, transform: 'translateX(0%)' }))
        ])
    ]);
};


