import { ActivatedRoute, Router } from '@angular/router';
import {
    ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef
} from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';

import { AuthenticationService } from '../../core/authentication.service';
import { getFadeCollapseTrigger } from '../../animations/fade-collapse.animaion';
import { HeightReturnComponent } from '../../shared/components/height-return/height-return.component';
import { UnauthorizedError } from '../../shared/errors/unauthorized.error';
import { HeaderService } from '../../core/header.service';

@Component({
    selector: 'app-login',
    templateUrl: 'login.component.html',
    styleUrls: ['login.component.css'],
    animations: [getFadeCollapseTrigger()],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class LoginComponent extends HeightReturnComponent {

    form: FormGroup = null;
    formSubmitted: boolean = false;

    get userNameOrEmailControl(): FormControl {
        return this.form.controls.userNameOrEmail as FormControl;
    }

    get passwordControl(): FormControl {
        return this.form.controls.password as FormControl;
    }

    authInProgress: boolean = false;
    authComplete: boolean = false;
    returnUrl: string = null;

    constructor(private _authService: AuthenticationService,
        private _cd: ChangeDetectorRef,
        private _headerService: HeaderService,
        private _router: Router,
        host: ElementRef,
        route: ActivatedRoute) {

        super(host);

        this.returnUrl = route.snapshot.queryParams.returnUrl
            || route.snapshot.queryParams.returnurl
            || route.snapshot.queryParams.return
            || null;

        this.form = new FormGroup({
            userNameOrEmail: new FormControl('', Validators.required),
            password: new FormControl('', Validators.required)
        });
    }

    getFormMessages(): string[] {
        let messages: string[] = [];
        if (this.form.errors) {
            for (let error in this.form.errors) {
                switch (error) {
                    case 'unauthorized':
                        messages.push('Введено неверное имя пользователя/email/пароль');
                        break;
                    default:
                        messages.push('Произошла неизвестная ошибка');
                        break;
                }
            }
        }
        return messages;
    }

    getUserNameOrEmailMessages(): string[] {
        let messages: string[] = [];
        if (this.form.controls.userNameOrEmail.errors) {
            for (let error in this.form.controls.userNameOrEmail.errors) {
                switch (error) {
                    case 'required':
                        messages.push('Введите ваше имя пользователя или email');
                        break;
                    default:
                        messages.push('Произошла неизвестная ошибка');
                        break;
                }
            }
        }
        return messages;
    }

    getPasswordMessages(): string[] {
        let messages: string[] = [];
        if (this.form.controls.password.errors) {
            for (let error in this.form.controls.password.errors) {
                switch (error) {
                    case 'required':
                        messages.push('Введите ваш пароль');
                        break;
                    default:
                        messages.push('Произошла неизвестная ошибка');
                        break;
                }
            }
        }
        return messages;
    }

    onSubmit(): void {
        this.formSubmitted = true;
        if (this.form.valid) {
            this.authInProgress = true;
            this._authService.login(
                this.userNameOrEmailControl.value, this.passwordControl.value)
                .subscribe(
                () => {
                    this.authComplete = true;
                    setTimeout(() => {
                        this._router.navigateByUrl(this.returnUrl || '/');
                    }, 1500);
                    this._headerService.close();
                    this._cd.markForCheck();
                },
                (error) => {
                    if (error instanceof UnauthorizedError) {
                        if (!this.form.errors) {
                            this.form.setErrors(
                                { 'unauthorized': true });
                        }
                        else {
                            this.form.errors['unauthorized'] = true;
                        }
                    }
                    this.authInProgress = false;
                    this._cd.markForCheck();
                });
        }
    }
}



