import {
    animate, animation, AnimationTriggerMetadata, query, stagger,
    style, transition, trigger, useAnimation
} from '@angular/animations';

const commonStyles = {
    position: 'absolute',
    width: '100%'
};

const initialAnimation = animation([
    query('tr:nth-of-type(even)', [
        style({ transform: 'translateX(-200%)' })
    ]),
    query('tr:nth-of-type(odd)', [
        style({ transform: 'translateX(200%)' })
    ]),
    query('tr', [
        stagger('50ms', [
            animate('600ms cubic-bezier(.07,.82,.39,.98)', style({ transform: 'translateX(0%)' }))
        ])
    ])
]);

const slideFromLeftAnimation = animation([
    style([commonStyles, { transform: 'translateX(-200%)' }]),
    animate('600ms ease-out', style({ transform: 'translateX(0%)' }))
]);

const slideToLeftAnimation = animation([
    style([commonStyles, { transform: 'translateX(0%)' }]),
    animate('600ms ease-out', style({ transform: 'translateX(-200%)' }))
]);

const slideFromRightAnimation = animation([
    style([commonStyles, { transform: 'translateX(200%)' }]),
    animate('600ms ease-out', style({ transform: 'translateX(0%)' }))
]);

const slideToRightAnimation = animation([
    style([commonStyles, { transform: 'translateX(0%)' }]),
    animate('600ms ease-out', style({ transform: 'translateX(200%)' }))
]);

const fadeOutAnimation = animation([
    style([commonStyles, { opacity: 1 }]),
    animate('300ms ease-out', style({ opacity: 0 }))
]);

const fadeInAnimation = animation([
    style([commonStyles, { opacity: 0 }]),
    animate('300ms 300ms ease-out', style({ opacity: 1 }))
]);

export function getPaginationTrigger(): AnimationTriggerMetadata {
    return trigger('pagination', [
        transition('void => initial', [
            useAnimation(initialAnimation)
        ]),
        transition('void => slideLeft', [
            useAnimation(slideFromRightAnimation)
        ]),
        transition('slideLeft => void', [
            useAnimation(slideToLeftAnimation)
        ]),
        transition('void => slideRight', [
            useAnimation(slideFromLeftAnimation)
        ]),
        transition('slideRight => void', [
            useAnimation(slideToRightAnimation)
        ]),
        transition('void => fade', [
            useAnimation(fadeInAnimation)
        ]),
        transition('fade => void', [
            useAnimation(fadeOutAnimation)
        ])
    ]);
};


