import {
    Directive, Input, TemplateRef, ViewContainerRef
} from '@angular/core';

import { Todo } from '../shared/models/todo.model';

@Directive({
    selector: '[appTodoItem]'
})
export class TodoItemDirective {

    @Input() todos: Todo[];

    constructor(private _container: ViewContainerRef,
        private _template: TemplateRef<TodoItemContext>) { }

    ngOnChanges(): void {
        this._container.clear();
        for (let i in this.todos) {
            this._container.createEmbeddedView(
                this._template, new TodoItemContext(this.todos[i]));
        }
    }
}

class TodoItemContext {
    constructor(public $implicit: Todo) { }
}


