import {
    animate, animateChild, group, query, state, style, transition, trigger
} from '@angular/animations';

export function getRoutingTrigger() {

    const commonStyles = {
        display: 'block',
        position: 'absolute',
        width: '100%'
    };

    return trigger('routing', [
        transition('todo => login, todo => signup, todo => accessDenied, todo => notFound, todo => accountInfo, createTodo => todo, editTodo => todo', [
            query(':leave', [
                style([commonStyles, { transform: 'translateX(0%)' }])
            ], { optional: true }),
            query(':enter', [
                style([commonStyles, { transform: 'translateX(-200%)' }])
            ], { optional: true }),
            group([
                query(':leave', [
                    animate('500ms ease-out',
                        style({ transform: 'translateX(200%)' })
                    )
                ], { optional: true }),
                query(':enter', [
                    animate('500ms ease-out',
                        style({ transform: 'translateX(0%)' }))
                ], { optional: true })
            ]),
        ]),
        transition('login => todo, signup => todo, accessDenied => todo, notFound => todo, accountInfo => todo, todo => createTodo, todo => editTodo', [
            query(':leave', [
                style([commonStyles, { transform: 'translateX(0%)' }])
            ], { optional: true }),
            query(':enter', [
                style([commonStyles, { transform: 'translateX(200%)' }])
            ], { optional: true }),
            group([
                query(':leave', [
                    animate('500ms ease-out',
                        style({ transform: 'translateX(-200%)' })
                    )
                ], { optional: true }),
                query(':enter', [
                    animate('500ms ease-out',
                        style({ transform: 'translateX(0%)' }))
                ], { optional: true })
            ]),
        ]),
        transition('* <=> *', [
            query(':leave', [
                style([commonStyles, { opacity: 1 }])
            ], { optional: true }),
            query(':enter', [
                style([commonStyles, { opacity: 0 }])
            ], { optional: true }),
            query(':leave', [
                animate('500ms ease-out',
                    style({ opacity: 0 }))
            ], { optional: true }),
            query(':enter', [
                animate('500ms ease-out',
                    style({ opacity: 1 }))
            ], { optional: true })
        ])
    ]);
};


