import { ActivatedRoute, Router } from '@angular/router';
import { AnimationEvent } from '@angular/animations';
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Subscription } from 'rxjs/Subscription';
import swal from 'sweetalert';

import { getPaginationTrigger } from '../../animations/pagination.trigger';
import { HeightReturnComponent } from '../../shared/components/height-return/height-return.component';
import { Todo } from '../../shared/models/todo.model';
import { TodoRepositoryService } from '../../core/todo-repository.service';
import { getFadeTrigger } from '../../animations/fade.trigger';

@Component({
    selector: 'app-todo-list',
    templateUrl: 'todo-list.component.html',
    styleUrls: ['todo-list.component.css'],
    animations: [getPaginationTrigger(), getFadeTrigger()],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class TodoListComponent extends HeightReturnComponent {

    paginatedTodos: Todo[];

    currentPage: number = 1;
    totalPages: number = 1;
    itemsPerPage: number = 10;
    paginationDisabled: boolean = false;

    paginationState: string = "fade";

    paginationTransitionCount: number = 0;
    outerContainerHeight: string = 'auto';

    paramsSubscription: Subscription;
    todosSubscription: Subscription;

    todosEmpty: boolean = false;
    firstRun: boolean = true;

    private _todos: Todo[];

    constructor(private _todoRepositoryService: TodoRepositoryService,
        private _route: ActivatedRoute,
        private _cd: ChangeDetectorRef,
        private _router: Router,
        host: ElementRef) {

        super(host);
    }

    ngOnInit(): void {
        this.todosSubscription = this._todoRepositoryService.todos.subscribe(todos => {
            this.paginationState = 'fade';
            this._cd.detectChanges();

            this._todos = todos;
            if (this._todos.length === 0)
                this.todosEmpty = true;
            this.totalPages = this.getTotalPages(this._todos, this.itemsPerPage);
            if (!this.firstRun) {
                this.paginatedTodos = this.getPaginatedTodos(this._todos, this.currentPage, this.itemsPerPage);
                if (this.paginatedTodos.length === 0 && this.currentPage > 1) {
                    this._router.navigate(['/todos', this.currentPage - 1]);
                }
                this._cd.detectChanges();
            }
            else {
                this.firstRun = false;
            }
        });

        this.paramsSubscription = this._route.params.subscribe(params => {
            let page: number = 1;
            if (params.page) {
                page = parseInt(params.page);
                if (isNaN(page)) page = 1;
            }

            if (Math.abs(this.currentPage - page) > 1) {
                this.paginationState = 'fade';
            }
            else if (this.currentPage > page) {
                this.paginationState = 'slideRight';
            }
            else {
                this.paginationState = 'slideLeft';
            }
            this._cd.detectChanges();
            this.currentPage = page;

            if (this._todos) {
                this.paginatedTodos = this.getPaginatedTodos(this._todos, this.currentPage, this.itemsPerPage);
                this._cd.detectChanges();
            }
        });
    }

    ngAfterViewInit(): void {
        super.ngAfterViewInit();
    }

    ngOnDestroy() {
        this._cd.detach();
        this.paramsSubscription.unsubscribe();
        this.todosSubscription.unsubscribe();
    }

    onPaginationStart(e: AnimationEvent) {
        this.paginationTransitionCount++;
        if (e.toState === 'void') {
            this.outerContainerHeight = this.getOuterHeight(e.element);
            this._cd.detectChanges();
        }
        if (e.fromState === 'void') {
            this.outerContainerHeight = this.getOuterHeight(e.element);
            this._cd.detectChanges();
        }
    }

    onPaginationDone(e: AnimationEvent) {
        this.paginationTransitionCount--;
        if (this.paginationTransitionCount === 0) {
            this.outerContainerHeight = 'auto';
            if (!this._cd['destroyed']) this._cd.detectChanges();
        }
    }

    getCurrentUrl(): string {
        return `/todos/${this.currentPage}`;
    }

    deleteTodo(id: number): void {
        swal({
            title: 'Подтвердите',
            text: 'Удалить todo',
            icon: 'warning',
            dangerMode: true,
            buttons: {
                cancel: {
                    text: "Отмена",
                    value: null,
                    visible: true,
                    className: "btn btn-outline-primary",
                    closeModal: true,
                },
                confirm: {
                    text: "OK",
                    value: true,
                    visible: true,
                    className: "btn btn-primary",
                    closeModal: true
                }
            }
        })
            .then(value => {
                if (value) {
                    this._todoRepositoryService.deleteTodo(id).subscribe(() => {
                        swal({
                            title: 'Готово',
                            text: 'Todo удалён',
                            icon: 'success',
                            buttons: {
                                confirm: {
                                    text: "OK",
                                    value: true,
                                    visible: true,
                                    className: "btn btn-primary",
                                    closeModal: true
                                }
                            }
                        });
                        this._cd.detectChanges();
                    });
                }
            });
    }

    doTodo(id: number): void {
        this._todoRepositoryService.doTodo(id).subscribe(() => {
            let todo = this._todos.find((todo) => {
                return todo.id === id;
            });
            todo.isDone = true;
            this._cd.markForCheck();
        });
    }

    private getTotalPages(todos: Todo[], itemsPerPage): number {
        return Math.ceil(todos.length / itemsPerPage);
    }

    private getPaginatedTodos(todos: Todo[], page: number, itemsPerPage: number): Todo[] {
        return todos.slice((page - 1) * itemsPerPage, page * itemsPerPage);
    }

    private getOuterHeight(elem: HTMLElement): string {
        let elemHeight: number, elemMargin: number;
        elemHeight = parseInt(document.defaultView.getComputedStyle(elem, '').getPropertyValue('height'));
        elemMargin = parseInt(document.defaultView.getComputedStyle(elem, '').getPropertyValue('margin-top'))
            + parseInt(document.defaultView.getComputedStyle(elem, '').getPropertyValue('margin-bottom'));
        return `${elemHeight + elemMargin}px`;
    }
}



