import { AfterViewInit, Component, ElementRef, HostBinding } from '@angular/core';
import { AsyncSubject } from 'rxjs/AsyncSubject';
import { Observable } from 'rxjs/Observable';

@Component({
    selector: 'app-height-return',
    templateUrl: 'height-return.component.html',
    styleUrls: ['height-return.component.css']
})
export class HeightReturnComponent implements AfterViewInit {

    @HostBinding('class.d-block') displayBlock: boolean = true;

    private _height: AsyncSubject<number> = null;
    private _viewInitComplete: boolean = false;

    constructor(protected _host: ElementRef) { }

    ngAfterViewInit(): void {
        this._viewInitComplete = true;
        if (this._height) {
            this._height.next(this._host.nativeElement.offsetHeight);
            this._height.complete();
        }
    }

    getHeight(): number | Observable<number> {
        if (this._viewInitComplete) {
            return this._host.nativeElement.offsetHeight;
        }
        else {
            if (!this._height) {
                this._height = new AsyncSubject<number>();
            }
            return this._height;
        }
    }
}



