import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/map';
import 'rxjs/add/observable/throw';

import { Todo } from '../shared/models/todo.model';
import { BadRequestError } from '../shared/errors/bad-request.error';
import { AppError } from '../shared/errors/app.error';
import { UnauthorizedError } from '../shared/errors/unauthorized.error';

@Injectable()
export class TodoRepositoryService {

    get todos(): Observable<Todo[]> {
        return this._todosSubject.asObservable();
    }

    todosLoaded: boolean = false;

    private _todosSubject: BehaviorSubject<Todo[]>;
    private _todos: Todo[] = [];

    private readonly _todoUrl: string = null;

    constructor(private _httpClient: HttpClient) {
        this._todoUrl = '/api/todos';
        this._todosSubject = new BehaviorSubject<Todo[]>([]);
    }

    loadTodos(): Observable<void> {
        return this._httpClient
            .get<Todo[]>(this._todoUrl)
            .do(todos => {
                this._todos = todos;
                this.todosLoaded = true;
                this._todosSubject.next(this._todos);
            })
            .map(() => { });
    }

    getTodo(id: number): Todo {
        if (this.todosLoaded) {
            return this._todos.find(t => t.id === id);
        }
    }

    createTodo(name: string, description: string): Observable<number> {
        let data = { name: name, description: description };
        return this._httpClient
            .post<number>(this._todoUrl, data)
            .do((id: number) => {
                let todo: Todo = {
                    id: id,
                    name: data.name,
                    description: data.description,
                    isDone: false
                };
                this._todos.push(todo);
                this._todosSubject.next(this._todos);
            })
            .catch((errorResponse: HttpErrorResponse) => {
                if (errorResponse.status == 400) {
                    return Observable.throw(new BadRequestError(errorResponse.error));
                }
                else if (errorResponse.status == 401) {
                    return Observable.throw(new UnauthorizedError());
                }
                else {
                    return Observable.throw(new AppError('��������� ����������� ������'));
                }
            });
    }

    updateTodo(id: number, changes: Array<{ op: 'replace', path: string, value: any }>): Observable<void> {
        return this._httpClient
            .patch(`${this._todoUrl}/${id}`, changes)
            .do(() => {
                let todo: Todo = this._todos.find(todo => todo.id === id);
                for (let i: number = 0; i < changes.length; i++) {
                    todo[changes[i].path] = changes[i].value;
                }
                this._todosSubject.next(this._todos);
            })
            .catch((errorResponse: HttpErrorResponse) => {
                if (errorResponse.status == 400) {
                    return Observable.throw(new BadRequestError(errorResponse.error));
                }
                else if (errorResponse.status == 401) {
                    return Observable.throw(new UnauthorizedError());
                }
                else {
                    return Observable.throw(new AppError('��������� ����������� ������'));
                }
            });
    }

    deleteTodo(id: number): Observable<void> {
        return this._httpClient
            .delete<void>(`${this._todoUrl}/${id}`)
            .do(() => {
                let index = this._todos.findIndex((todo) => {
                    return todo.id === id;
                });
                this._todos.splice(index, 1);
                this._todosSubject.next(this._todos);
            });
    }

    doTodo(id: number): Observable<void> {
        return this._httpClient
            .post<void>(`${this._todoUrl}/do/${id}`, null)
            .do(() => {
                let todo = this._todos.find((todo) => {
                    return todo.id === id;
                });
                todo.isDone = true;
            });
    }

    clearTodos(): void {
        this._todos = [];
        this.todosLoaded = false;
    }
}



