import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';

import { TodoRepositoryService } from '../core/todo-repository.service';

@Injectable()
export class TodoGuard implements CanActivate {

    constructor(private _todoRepositoryService: TodoRepositoryService) { }

    canActivate(
        next: ActivatedRouteSnapshot,
        state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {

        if (this._todoRepositoryService.todosLoaded) return true;
        return this._todoRepositoryService.loadTodos().map(() => true);
    }
}



