import { Component, ElementRef, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { HeightReturnComponent } from '../../shared/components/height-return/height-return.component';
import { UserRepositoryService } from '../../core/user-repository.service';
import { UserDetails } from '../../shared/models/user-details.model';

@Component({
    selector: 'app-account-info',
    templateUrl: 'account-info.component.html',
    styleUrls: ['account-info.component.css'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class AccountInfoComponent extends HeightReturnComponent {

    get user(): UserDetails {
        return this._user;
    }

    private _user: UserDetails;

    constructor(private _userRepositoryService: UserRepositoryService,
        private _cd: ChangeDetectorRef,
        host: ElementRef) {

        super(host);
    }

    ngOnInit(): void {
        this._userRepositoryService.user.subscribe(user => {
            this._user = user;
            this._cd.markForCheck();
        });
    }
}



