import { animate, trigger, transition, group, style } from '@angular/animations';

export function getFadeCollapseTrigger() {

    return trigger('fadeCollapse', [
        transition(':enter', [
            style({ height: '0', opacity: 0 }),
            group([
                animate('.2s', style({ height: '*' })),
                animate('.4s', style({ opacity: 1 }))
            ]),
        ]),
        transition(':leave', [
            style({ height: '*', opacity: 1 }),
            group([
                animate('.1s', style({ opacity: 0 })),
                animate('.2s', style({ height: '0px' }))
            ])
        ]),
    ]);
};



