import { Component, ElementRef, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';

import { HeightReturnComponent } from '../../shared/components/height-return/height-return.component';
import { TodoRepositoryService } from '../../core/todo-repository.service';
import { Todo } from '../../shared/models/todo.model';
import { getFadeCollapseTrigger } from '../../animations/fade-collapse.animaion';

@Component({
    selector: 'app-todo-edit',
    templateUrl: 'todo-edit.component.html',
    animations: [getFadeCollapseTrigger()],
    styleUrls: ['todo-edit.component.css'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class TodoEditComponent extends HeightReturnComponent {

    form: FormGroup = null;
    formSubmitted: boolean = false;
    mode: 'edit' | 'create' = null;
    inProgress: boolean = false;
    header: string = '';
    success: boolean = false;
    successMessage: string = null;

    private _todo: Todo = null;

    get nameControl(): FormControl {
        return this.form.controls.name as FormControl;
    }

    get descriptionControl(): FormControl {
        return this.form.controls.description as FormControl;
    }

    returnUrl: string = null;

    constructor(private _todoRepositoryService: TodoRepositoryService,
        private _router: Router,
        private _route: ActivatedRoute,
        private _cd: ChangeDetectorRef,
        host: ElementRef) {

        super(host);
    }

    ngOnInit(): void {
        this.returnUrl = this._route.snapshot.queryParams.returnUrl
            || this._route.snapshot.queryParams.returnurl
            || this._route.snapshot.queryParams.return
            || 'todos/1';

        if (this._route.snapshot.url[0].path === 'edit-todo') {
            this.mode = 'edit';
            this.header = 'Редактировать todo';
            if (this._route.snapshot.queryParams.id) {
                let id: number = parseInt(this._route.snapshot.queryParams.id);
                if (isNaN(id)) {
                    this._router.navigateByUrl(this.returnUrl);
                }
                else {
                    let todo: Todo = this._todoRepositoryService.getTodo(id);
                    if (todo == null) {
                        this._router.navigateByUrl('not-found');
                    }
                    else {
                        this._todo = todo;
                    }
                }
            }
            else {
                this._router.navigateByUrl(this.returnUrl);
            }
        }
        else {
            this.mode = 'create';
            this.header = 'Создать todo';
        }

        this.form = new FormGroup({
            name: new FormControl('', Validators.compose(
                [Validators.maxLength(50)])),
            description: new FormControl('', Validators.compose(
                [Validators.required, Validators.maxLength(500)]))
        });

        if (this.mode === 'edit' && this._todo) {
            this.nameControl.setValue(this._todo.name);
            this.descriptionControl.setValue(this._todo.description);
        }
    }

    getNameMessages(): string[] {
        let messages: string[] = [];
        if (this.form.controls.name.errors) {
            for (let error in this.form.controls.name.errors) {
                switch (error) {
                    case 'maxlength':
                        messages.push('Название слишком длинное');
                        break;
                    default:
                        messages.push('Произошла неизвестная ошибка');
                        break;
                }
            }
        }
        return messages;
    }

    getDescriptionMessages(): string[] {
        let messages: string[] = [];
        if (this.form.controls.description.errors) {
            for (let error in this.form.controls.description.errors) {
                switch (error) {
                    case 'required':
                        messages.push('Введите описание');
                        break;
                    case 'maxlength':
                        messages.push('Описание слишком длинное');
                        break;
                    default:
                        messages.push('Произошла неизвестная ошибка');
                        break;
                }
            }
        }
        return messages;
    }

    onSubmit(): void {
        this.formSubmitted = true;
        if (this.form.valid) {
            this.inProgress = true;
            if (this.mode === 'create') {
                this._todoRepositoryService.createTodo(
                    this.nameControl.value, this.descriptionControl.value)
                    .subscribe(
                    () => {
                        this.success = true;
                        this.successMessage = 'Todo создан!';
                        setTimeout(() => {
                            this._router.navigateByUrl(this.returnUrl || '/');
                        }, 2000);
                        this._cd.markForCheck();
                    },
                    (error) => {
                        this.inProgress = false;
                        this._cd.markForCheck();
                    });
            }
            else {
                let changes: any[] = [];
                if (this.nameControl.dirty) {
                    changes.push({ op: 'replace', path: 'name', value: this.nameControl.value });
                }
                if (this.descriptionControl.dirty) {
                    changes.push({ op: 'replace', path: 'description', value: this.descriptionControl.value });
                }
                if (changes.length == 0) {
                    this._router.navigateByUrl(this.returnUrl);
                }
                else {
                    this._todoRepositoryService.updateTodo(
                        this._todo.id, changes)
                        .subscribe(
                        () => {
                            this.success = true;
                            this.successMessage = 'Todo изменён!';
                            setTimeout(() => {
                                this._router.navigateByUrl(this.returnUrl || '/');
                            }, 2000);
                            this._cd.markForCheck();
                        },
                        (error) => {
                            this.inProgress = false;
                            this._cd.markForCheck();
                        });
                }
            }
        }
    }
}



