import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';

import { AuthenticationService } from '../core/authentication.service';

@Injectable()
export class AdminGuard implements CanActivate {

    constructor(private _authService: AuthenticationService,
        private _router: Router) { }

    canActivate(
        next: ActivatedRouteSnapshot,
        state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
        let authState: boolean | Observable<boolean> = this._authService.getAuthState();
        if (authState instanceof Observable) {
            return authState.map((isAuthenticated) => {
                if (isAuthenticated && this._authService.user.details.isAdministrator) {
                    return true;
                }
                else if (!isAuthenticated) {
                    this._router.navigate(['/login'], { queryParams: { return: state.url } });
                    return false;
                }
                else {
                    this._router.navigate(['/access-denied']);
                    return false;
                }
            });
        }
        else if (authState && this._authService.user.details.isAdministrator) {
            return true;
        }
        else if (authState && !this._authService.user.details.isAdministrator) {
            this._router.navigate(['/access-denied']);
        }
        else if (!authState) {
            this._router.navigate(['/login'], { queryParams: { return: state.url } });
        }
        return false;
    }
}



