import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import { UserRepositoryService } from '../core/user-repository.service';
import { AuthenticationService } from '../core/authentication.service';

@Injectable()
export class AccountInfoGuard implements CanActivate {

    constructor(private _userRepo: UserRepositoryService,
        private _authService: AuthenticationService,
        private _router: Router) { }

    canActivate(
        next: ActivatedRouteSnapshot,
        state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {

        if (this._authService.user.details && this._authService.user.details.id) {
            let id: string = this._authService.user.details.id;
            return this._userRepo.loadUser(id).map(() => true);
        }
        this._router.navigateByUrl('/');
    }
}



