import { ActivatedRoute, Router } from '@angular/router';
import {
    Component, ChangeDetectionStrategy, ChangeDetectorRef, ElementRef
} from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';

import { getFadeCollapseTrigger } from '../../animations/fade-collapse.animaion';
import { HeightReturnComponent } from '../../shared/components/height-return/height-return.component';
import { UnprocessableEntityError } from '../../shared/errors/uprocessable-entity.error';
import { UserRepositoryService } from '../../core/user-repository.service';
import { AuthenticationService } from '../../core/authentication.service';
import { HeaderService } from '../../core/header.service';

@Component({
    selector: 'app-signup',
    templateUrl: 'signup.component.html',
    styleUrls: ['signup.component.css'],
    animations: [getFadeCollapseTrigger()],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class SignupComponent extends HeightReturnComponent {

    form: FormGroup;
    formSubmitted: boolean = false;

    get userNameControl(): FormControl {
        return this.form.controls.userName as FormControl;
    }

    get emailControl(): FormControl {
        return this.form.controls.email as FormControl;
    }

    get passwordControl(): FormControl {
        return this.form.controls.password as FormControl;
    }

    get passwordConfirmControl(): FormControl {
        return this.form.controls.passwordConfirm as FormControl;
    }

    accountCreationInProgress: boolean = false;
    accountCreated: boolean = false;
    returnUrl: string = null;

    constructor(private _userRepositoryService: UserRepositoryService,
        private _authService: AuthenticationService,
        private _cd: ChangeDetectorRef,
        private _headerService: HeaderService,
        private _router: Router,
        host: ElementRef,
        route: ActivatedRoute) {

        super(host);

        this.returnUrl = route.snapshot.queryParams.returnUrl
            || route.snapshot.queryParams.returnurl
            || route.snapshot.queryParams.return
            || null;

        this.form = new FormGroup({
            userName: new FormControl('', Validators.compose(
                [this.validateUserName, Validators.minLength(3), Validators.maxLength(25)])),
            email: new FormControl('', Validators.compose(
                [Validators.required, Validators.email])),
            password: new FormControl('', Validators.compose(
                [Validators.required, Validators.minLength(6), Validators.maxLength(25), this.validatePassword])),
            passwordConfirm: new FormControl('')
        });

        this.passwordConfirmControl.setValidators(Validators.compose(
            [Validators.required, this.compareControlValues(this.passwordControl)]));

        this.passwordControl.valueChanges.subscribe(() => {
            this.passwordConfirmControl.updateValueAndValidity();
        });
    }

    getUserNameMessages(): string[] {
        let messages: string[] = [];
        if (this.userNameControl.errors) {
            if (this.userNameControl.errors.noalphanumeric) {
                messages.push('Имя пользователя может содержать только английские буквы и цифры');
            }
            else if (this.userNameControl.errors.minlength) {
                messages.push(`Имя пользователя должно содержать не менее ${this.userNameControl.errors.minlength.requiredLength} символов`);
            }
            else if (this.userNameControl.errors.maxlength) {
                messages.push('Имя пользователя слишком длинное');
            }
            else if (this.userNameControl.errors.duplicate) {
                messages.push('Это имя пользователя уже занято');
            }
            else {
                messages.push('Произошла неизвестная ошибка');
            }
        }
        return messages;
    }

    getEmailMessages(): string[] {
        let messages: string[] = [];
        if (this.emailControl.errors) {
            if (this.emailControl.errors.required) {
                messages.push('Введите ваш электронный адрес');
            }
            else if (this.form.controls.email.errors.email) {
                messages.push('Введён электронный адрес неверного формата');
            }
            else {
                messages.push('Произошла неизвестная ошибка');

            }
            return messages;
        }
    }

    getPasswordMessages(): string[] {
        let messages: string[] = [];
        if (this.passwordControl.errors) {
            if (this.passwordControl.errors.required) {
                messages.push('Введите пароль');
            }
            if (messages.length > 0) return messages;

            if (this.passwordControl.errors.alphanumeric) {
                messages.push('Пароль должен содержать хотя бы один не буквенно-цифровой символ');
            }
            if (this.passwordControl.errors.digit) {
                messages.push('Пароль должен содержать хотя бы одну цифру');
            }
            if (this.passwordControl.errors.uppercase) {
                messages.push('Пароль должен содержать хотя бы одну заглавную английскую букву');
            }
            if (messages.length > 0) return messages;

            if (this.passwordControl.errors.minlength) {
                messages.push(`Пароль должен содержать не менее ${this.passwordControl.errors.minlength.requiredLength} символов`);
            }
            if (messages.length > 0) return messages;

            if (this.passwordControl.errors.maxlength) {
                messages.push(`Пароль слишком длинный`);
            }
            if (messages.length > 0) return messages;

            messages.push('Произошла неизвестная ошибка');
        }
        return messages;
    }

    getPasswordConfirmMessages(): string[] {
        let messages: string[] = [];
        if (this.passwordConfirmControl.errors) {
            if (this.passwordConfirmControl.errors.required) {
                messages.push('Подтвердите пароль');
            }
            if (messages.length > 0) return messages;

            if (this.passwordConfirmControl.errors.match) {
                messages.push('Пароли не совпадают');
            }
            if (messages.length > 0) return messages;

            messages.push('Произошла неизвестная ошибка');
        }
        return messages;
    }

    onSubmit(): void {
        this.formSubmitted = true;
        if (this.form.valid) {
            this.accountCreationInProgress = true;
            let name = this.userNameControl.value;
            let email = this.emailControl.value;
            let password = this.passwordControl.value;
            this._userRepositoryService.createUser(
                name, email, password)
                .subscribe(
                () => {
                    this.accountCreated = true;
                    this._authService.login(name || email, password)
                        .subscribe(() => {
                            this._headerService.close();
                            setTimeout(() => {
                                this._router.navigateByUrl(this.returnUrl || '/');
                            }, 2000);
                        });
                    this._cd.markForCheck();
                },
                (error) => {
                    if (error instanceof UnprocessableEntityError) {
                        if (error.details.UserName && error.details.UserName.findIndex(
                            (msg: string) => msg === 'DuplicateUserName') != -1) {
                            if (!this.userNameControl.errors) {
                                this.userNameControl.setErrors({ 'duplicate': true });
                            }
                            else {
                                this.userNameControl.errors.duplicate = true;
                            }
                        }
                        this.accountCreationInProgress = false;
                        this._cd.markForCheck();
                    }
                });
        }
    }

    private validateUserName(control: FormControl): { [key: string]: any } {
        if (/\W/.test(control.value)) {
            return { 'noalphanumeric': true };
        }
        return null;
    }

    private validatePassword(control: FormControl): { [key: string]: any } {
        let errors: { [key: string]: any } = null;
        if (!/\W/.test(control.value)) {
            if (!errors) errors = {};
            errors['alphanumeric'] = true;
        }
        if (!/[0-9]/.test(control.value)) {
            if (!errors) errors = {};
            errors['digit'] = true;
        }
        if (!/[A-Z]/.test(control.value)) {
            if (!errors) errors = {};
            errors['uppercase'] = true;
        }
        return errors;
    }

    private compareControlValues(passwordControl: FormControl)
        : (control: FormControl) => { [key: string]: any } {

        return (control: FormControl): { [key: string]: any } => {
            if (control.value === passwordControl.value) {
                return null;
            }
            else {
                return { 'match': true };
            }
        };
    }
}



