import { Injectable } from '@angular/core';
import {
    ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot
} from '@angular/router';
import { Observable } from 'rxjs/Observable';

import { AuthenticationService } from '../core/authentication.service';
import { TodoRepositoryService } from '../core/todo-repository.service';
import { UserRepositoryService } from '../core/user-repository.service';

@Injectable()
export class LogoutGuard implements CanActivate {

    constructor(private _authService: AuthenticationService,
        private _todoRepositoryService: TodoRepositoryService,
        private _userRepositoryService: UserRepositoryService,
        private _router: Router) { }

    canActivate(next: ActivatedRouteSnapshot, state: RouterStateSnapshot)
        : Observable<boolean> | Promise<boolean> | boolean {

        this._authService.logout();
        this._todoRepositoryService.clearTodos();
        this._userRepositoryService.clearUsers();
        this._router.navigateByUrl('/login');
        return false;
    }
}



